import React from 'react';
import Navlists from '../components/Navlists';

function NavigationBar() {
  const textColr = 'text-dark';
  return (
    <nav
      className="navbar navbar-expand-lg navbar-dark fixed-top  bg-transparent "
      id="mainNav"
    >
      <div className="container">
        <a
          className={`navbar-brand js-scroll-trigger ${textColr}`}
          href="#page-top"
        >
          Busry
        </a>
        <button
          className="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarResponsive"
          aria-controls="navbarResponsive"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <Navlists textColr={textColr} />
      </div>
    </nav>
  );
}

export default NavigationBar;
